  content: "";
  position: absolute;
  left: 0;
  border-radius: inherit;
  width: var(--burger-width);
  height: var(--burger-line-height);
  background-color: currentcolor;
}

.burger-button::before {
  top: 13px;
}

.burger-button::after {
  top: 30px;
}

.burger-button__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: var(--burger-width);
  height: var(--burger-line-height);
  background-color: currentcolor;
  transform: translateY(-50%);
}

